import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from "./pages/auth/login/login.component";
import { ForgotPasswordComponent } from "./pages/auth/forgot-password/forgot-password.component";
import { ResponsePdfComponent } from './pages/gropus/response-pdf.component';
import { NotificationComponent } from './pages/notification/notification.component';
import { QuestionsPdfComponent } from './pages/gropus/questions-pdf.component';
import { RespondantPdfComponent } from './pages/gropus/respondant-pdf/respondant-pdf.component';
import { AggregatePdfComponent } from './pages/gropus/aggregate-pdf/aggregate-pdf.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: '/login' },
  { path: 'login', component: LoginComponent },
  { path: 'forgot-password', component: ForgotPasswordComponent },
  { path:'response-pdf',component:ResponsePdfComponent},
  {
    path: 'notification',
    component:NotificationComponent
  },
  {
    path:'questions-pdf',
    component:QuestionsPdfComponent
  },
  {
    path:'respondant-pdf',
    component:RespondantPdfComponent
  },
  {
    path: 'aggregate-pdf',
    component:AggregatePdfComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
