import { Component, ElementRef, ViewChild } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { Configuration } from "src/app/app.configuration";
import { DataService } from "src/app/shared/services/data-service";
import * as html2pdf from 'html2pdf.js';

@Component({
  selector: "app-respondant-pdf",
  templateUrl: "./respondant-pdf.component.html",
  styleUrls: ["./respondant-pdf.component.scss"],
})
export class RespondantPdfComponent {
  templateId: any;
  participantId: any;
  memberId: any;
  memberQuestionsArr: any = [];
  memberName: any;
  @ViewChild('content', { static: false }) el!: ElementRef

  constructor(
    private dataService: DataService,
    private configuration: Configuration,
    private activateRouter: ActivatedRoute
  ) { }

  addLineBreak(event: KeyboardEvent): void {
    if (event.key === 'Enter') {
      const paragraph = document.getElementById('editable-paragraph');

      if (paragraph) {
        const selection = window.getSelection();
        const range = selection.getRangeAt(0);
        const br = document.createElement('br');
        range.insertNode(br);
        range.setStartAfter(br);
        range.setEndAfter(br);
        selection.removeAllRanges();
        selection.addRange(range);
        event.preventDefault();
      }
    }
  }

  ngOnInit() {

    this.activateRouter.queryParams.subscribe((queryParams) => {
      this.templateId = queryParams.temp;
      this.participantId = queryParams.partId;
      this.memberId = queryParams.ID;
    });
    this.dataService.getMemberResponse(this.configuration.memberResponse, this.memberId).subscribe((res: any) => {
      this.memberName = res.Name
      this.memberQuestionsArr = res.data;
    });
  }

  

  generatePDF() {
    const content = this.el.nativeElement;
    const options = {
      margin: 10,
      filename: 'my-document.pdf',
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' }
    };
    const data = html2pdf().set({
      pagebreak: { mode: 'avoid-all' }
    }).from(content).set(options).outputPdf().save()
  }
}
