
import { Component, ElementRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as html2pdf from 'html2pdf.js';
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { ExcelService } from 'src/app/shared/services/excel-service';
@Component({
  selector: 'app-response-pdf',
  templateUrl: './response-pdf.component.html',
  styleUrls: ['./response-pdf.component.scss']
})
export class ResponsePdfComponent {
  user:string = ' '
  feedbackContent:any
  @ViewChild('content',{static:false})el!:ElementRef
  isEdit: boolean = false;
  id: number;
  groupID: any;
  final: any;
  orgName: any;
  tempID: any;
  resultPDFid: any;
  constructor(public excel:ExcelService, public activeRouter : ActivatedRoute, private router: Router,private dataService:DataService, private configuration:Configuration
    ){

    // this.feedbackContent = this.excel.getString()
  }

  ngOnInit(): void {
   
    this.activeRouter.queryParams.subscribe((queryParams) => {
      this.isEdit = queryParams.isEdit === 'true';
      this.id = queryParams.ID; // Parse as an integer
      this.orgName = queryParams.OrgName
      this.groupID = queryParams.groupId
      this.tempID = queryParams.temp
    
    });
    this.getParticipe()
  }
  
  getParticipe(){
    const apiUrl = this.configuration.participe

    this.dataService.getById(apiUrl,this.id).subscribe((data:any)=>{
     
      let membersData = data?.data[0]
      if(membersData?.Result_pdf){
       
        this.feedbackContent = membersData?.Result_pdf[0]?.text
      }
      
      this.user = membersData?.participantFirstName + " " + membersData?.participantLastName
   
  })
  }

  isHeader(line: string): boolean {
    const headerKeywords = ["Strengths", "Weaknesses"];
    if ( line.trim().endsWith(":")) {
      return true;
    }
    for (const keyword of headerKeywords) {
      if (line.includes(keyword)) {
        return true;
      }
    }
    return false;
  }
back(){
  this.router.navigate(['/map/groups/howard-members'],{ queryParams: { isEdit: this.isEdit,ID: this.id,groupId:this.groupID, OrgName:  this.orgName,temp: this.tempID}});
 
  
}
   generatePDF() {
    const content = this.el.nativeElement;
   
    const options = {
      margin: 10,
      filename: 'my-document.pdf',
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' }
    };
  const data =   html2pdf().set({
      pagebreak: { mode: 'avoid-all' }
    }).from(content).set(options).outputPdf().save() 
    
  }
}