import { Component, OnInit, ChangeDetectorRef, TemplateRef, ViewChild } from '@angular/core';
import { Router } from "@angular/router";
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { MediaMatcher } from '@angular/cdk/layout';
import { ToastrService } from 'ngx-toastr';
import { MatDialog } from '@angular/material/dialog';
import { GetProfileImageService } from 'src/app/shared/helper/get-profile-image';

@Component({
    templateUrl: './master-page.component.html',
    styleUrls: ['./master-page.component.scss']
})

export class MasterPageComponent implements OnInit {
    searchText: string = ''; 
    mobileQuery: MediaQueryList;
    isCollapsed = false;
    role: any;
    userName: any;
    todayDate = new Date();
    currentHeaderName: any;
    toggleOpen = true;
    toggleSidebar = true;
    Addpopup: any;
    @ViewChild("logoutpopup", { static: false }) logoutpopup: TemplateRef<any>;
    imageURL = 'assets/images/profile.jpg'
    private _mobileQueryListener: () => void;
    email: any;
    proImage: any;
    constructor(private router: Router,
        private storageConfiguration: StorageConfiguration,
        private toastr: ToastrService,
        changeDetectorRef: ChangeDetectorRef, media: MediaMatcher,
        public dialog: MatDialog,
        private getProfileImageService: GetProfileImageService
    ) {

        this.mobileQuery = media.matchMedia('(max-width: 600px)');
        this._mobileQueryListener = () => changeDetectorRef.detectChanges();
        this.mobileQuery.addListener(this._mobileQueryListener);

    }

    ngOnInit(): void {
        this.userName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.userName);
        this.role = this.storageConfiguration.sessionGetItem(this.storageConfiguration.role);
        this.email = this.storageConfiguration.sessionGetItem(this.storageConfiguration.email);
        this.proImage = this.storageConfiguration.sessionGetItem(this.storageConfiguration.profileImg);
        this.proImage = this.proImage == 'undefined' ? null : this.proImage;
        let url = this.router.url.split('/');
        this.currentHeaderName = url[2];

        this.getProfile();
    }

    getProfile() {
        this.getProfileImageService.getProfileImage().subscribe((data => {
            this.proImage = this.storageConfiguration.sessionGetItem(this.storageConfiguration.profileImg);
            this.proImage = this.proImage == 'undefined' ? null : this.proImage;
        }));

    }

    onSearch(): void {
      }


    toggleClick() {
        //   this.toggleOpen = !this.toggleOpen;
        this.toggleSidebar = !this.toggleSidebar;
    }

    updateProfile() {
        let id = this.storageConfiguration.sessionGetItem(this.storageConfiguration.id);
        if (this.role === 'super-admin') {
            this.router.navigate(['/map/update-profile'], { queryParams: { userId: id, isEdit: true, isSuperAdmin: true } });
        }
        else {
            this.router.navigate(['/map/update-profile'], { queryParams: { userId: id, isEdit: true, isSuperAdmin: false } });
        }
    }

    changePassword() {
        if (this.role === 'Admin' || this.role === 'super-admin') {
            this.router.navigate(['/map/change-password']);
        }
        else {
            this.router.navigate(['/map/change-password']);
        }
    }
    signoutpopup() {
        this.Addpopup = this.dialog.open(this.logoutpopup, {
            width: '350px',
            height: '175px'
        });
    }


    signOut() {
        this.router.navigate(['/login']);
        this.Addpopup.close();
        this.toastr.success('Logout Successfully');
        localStorage.clear();
    }

    closeDialog() {
        this.Addpopup.close();
    }

}
