import { Injectable } from '@angular/core';
import { DataService } from '../services/data-service';
import { Configuration } from 'src/app/app.configuration';
import { Subject, Observable } from 'rxjs';
import { StorageConfiguration } from '../storage-setting/storage-configuration';

@Injectable({
    providedIn: 'root'
})

export class GetProfileImageService {
    subject = new Subject<any>();

    constructor(
        private dataService: DataService,
        private configuration: Configuration,
        private storageConfiguration: StorageConfiguration,
    ) {

    }

    getProfileImage(): Observable<any> {
        this.subject.next(this.storageConfiguration.sessionGetItem(this.storageConfiguration.profileImg));
        return this.subject.asObservable();
    }



}