import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ConfirmDialogComponent } from '../confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-custom-analyze',
  templateUrl: './custom-analyze.component.html',
  styleUrls: ['./custom-analyze.component.scss']
})
export class CustomAnalyzeComponent implements OnInit {

  categories = [{ name: 'Communication', checked: true }, { name: 'Controlling', checked: true }, { name: 'Leading', checked: true }, { name: 'Organizing', checked: true }, { name: 'Planning', checked: true }, { name: 'Staffing', checked: true }]

  global = true;
  your = true;

  constructor(
    public dialogRef: MatDialogRef<ConfirmDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private _data: any,
  ) { }

  ngOnInit() {
    this.categories = this._data;
  }

  closeDialog() {
    let sendData = {
      response: false,
      data: this.categories
    }
    this.dialogRef.close(sendData)
  }
  confirmDialog() {
    let analyzeType;
    if (this.global == true && this.your == true) {
      analyzeType = 'all';
    }
    else {
      if (this.global == true) {
        analyzeType = 'global';
      }
      if (this.your == true) {
        analyzeType = 'your';
      }
    }
    let sendData = {
      response: true,
      data: this.categories,
      analyzeType: analyzeType
    }
    this.dialogRef.close(sendData)
  }

  checkboxChange(index) {
    this.categories[index].checked = !this.categories[index].checked;
  }
}
