    uploadPdfAndData(pdfFile: File, userQuestion: string, pdfName: string) {
        const formData = new FormData();

        formData.append('file', pdfFile);
        formData.append('question', userQuestion);
        formData.append('pdf_name', pdfName);
        return this.http.post(this.apiBackendUrl, formData);
      }

      sendResult(email: string, url: string) {
        const headers = new HttpHeaders().set('type', 'resultPdf');
        const formData = new FormData();
        // formData.append('file', pdfFile);
        formData.append('email', email);
        formData.append('url', url)
        return this.http.post(this.sendResultUrl, formData, { headers });
      }
      
