import { Component, OnInit } from "@angular/core";
import { UntypedFormGroup, Validators, UntypedFormBuilder } from "@angular/forms";
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { Configuration } from 'src/app/app.configuration';
import { Router } from "@angular/router";
import { Location } from '@angular/common';
import { ToastrService } from 'ngx-toastr';

@Component({
    templateUrl: "./update-profile.component.html",
    styleUrls: ["./update-profile.component.scss"]
})

export class UpdateProfileComponent implements OnInit {

    userForm: UntypedFormGroup
    selectedRole = 'admin';
    id: any;
    userName: any;
    roles = [];
    addressInfo: any;
    roleName: any;

    constructor(private fb: UntypedFormBuilder,
        private toastr: ToastrService,
        private storageConfiguration: StorageConfiguration,
        private dataService: DataService,
        private configuration: Configuration,
        private router: Router,
        private _location: Location
    ) {
        this.userForm = this.fb.group({
            emailId: ['', Validators.required],
            role: ['', Validators.required],
            firstName: ['', Validators.required],
            lastName: ['', Validators.required],
            address: ['', Validators.required],
            mobile: ['', Validators.required],
            city: ['', Validators.required],
            state: ['', Validators.required],
            zipCode: ['', Validators.required],
            country: ['', Validators.required],
        });
    }

    ngOnInit() {
        this.userName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.userName);
        this.roleName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.name);
        this.getRoles();
        this.id = this.storageConfiguration.sessionGetItem(this.storageConfiguration.id);
        this.editUser();
    }

    editUser() {
        this.dataService.getSingleUser(this.id, this.configuration.user + '/').subscribe(
            (data: any) => {
                let personalInfo = data.data;
                let addressInfo = personalInfo.address;
                this.userForm.patchValue({
                    emailId: personalInfo.email,
                    password: personalInfo.password,
                    role: personalInfo.role._id,
                    firstName: personalInfo.firstName,
                    lastName: personalInfo.lastName,
                    mobile: addressInfo.mobile,
                    address: addressInfo.addressLine,
                    city: addressInfo.city,
                    state: addressInfo.state,
                    zipCode: addressInfo.zipcode,
                    country: addressInfo.country
                });
            }, err => {
            }
        );
    }

    updateProfile() {
        if (this.userForm.valid) {
            let userForm = this.userForm.value;
            this.addressInfo = {
                addressLine: userForm.address,
                mobile: userForm.mobile,
                city: userForm.city,
                state: userForm.state,
                zipcode: userForm.zipCode,
                country: userForm.country
            };
            let userModel = {
                firstName: userForm.firstName,
                lastName: userForm.lastName,
                fullName: userForm.firstName + ' ' + userForm.lastName,
                email: userForm.emailId,
                addressInfo: this.addressInfo,
                role: userForm.role
            }
            this.dataService.updateUser(this.id, this.configuration.user + '/', userModel).subscribe(
                (data: any) => {
                    this.toastr.success('Profile updated successfully.');
                }, err => {
                }
            );
        }
        else {
            for (const i in this.userForm.controls) {
                this.userForm.controls[i].markAsDirty();
                this.userForm.controls[i].updateValueAndValidity();
            }
            this.toastr.error('Please fill all required fields');
        }
    }

    async getRoles() {
        this.roles = [];
        await this.dataService.getAll(this.configuration.roles).subscribe(
            (data: any) => {
                this.roles = data.data.data;
            });
    }

    backlocation() {
        this._location.back();
    }

    keyPress(event: any) {
        const pattern = /[0-9\+\ ]/;
        let inputChar = String.fromCharCode(event.charCode);
        if (event.keyCode != 8 && !pattern.test(inputChar)) {
            event.preventDefault();
        }
    }

}