import { Component, ElementRef, TemplateRef, ViewChild } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute, Router } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { Configuration } from "src/app/app.configuration";
import { DataService } from "src/app/shared/services/data-service";
import { ExcelService } from "src/app/shared/services/excel-service";
import * as html2pdf from 'html2pdf.js';


@Component({
  selector: "app-questions-pdf",
  templateUrl: "./questions-pdf.component.html",
  styleUrls: ["./questions-pdf.component.scss"],
})
export class QuestionsPdfComponent {
  user:string = ' ff'
  participantId: any = [];
  @ViewChild('content',{static:false})el!:ElementRef
  membersData: any;
  questionAnsArr: any;
  quesName: any = [];
  quesAnswerName: any = [];
  ques: any;
  quesAns: any;
  templateId: any;
  q_id: any;
  qArray: any = [];
  questionById: any = []
  q_answer: any;
  quesByAnswer: any;
  participantName: any;

  constructor(
    private dataService: DataService,
    private fb: FormBuilder,
    private route: Router,
    public excel: ExcelService,
    public toastr: ToastrService,
    private dialog: MatDialog,
    public activeRouter: ActivatedRoute,
    public configuration: Configuration
  ) {}

  async ngOnInit() {
    this.activeRouter.queryParams.subscribe((queryParams) => {
     
      this.templateId = queryParams.temp;
      this.participantId = queryParams.partId;
    });
    
    await this.dataService
      .getParticipantResponse(
        this.configuration.participantRes,
        this.participantId
      )
      .subscribe((res:any) => {
        this.participantName = res.Name;
       
        this.qArray =  res.data;
      });
  }

  addLineBreak(event: KeyboardEvent): void {
    if (event.key === 'Enter') {
      const paragraph = document.getElementById('editable-paragraph');

      if (paragraph) {
        const selection = window.getSelection();
        const range = selection.getRangeAt(0);
        const br = document.createElement('br');
        range.insertNode(br);
        range.setStartAfter(br);
        range.setEndAfter(br);
        selection.removeAllRanges();
        selection.addRange(range);
        event.preventDefault();
      }
    }
  }

  generatePDF() {
    const content = this.el.nativeElement;
   
    const options = {
      margin: 10,
      filename: 'my-document.pdf',
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' }
    };
  const data =   html2pdf().set({
      pagebreak: { mode: 'avoid-all' }
    }).from(content).set(options).outputPdf().save() 
   
  }
}
