import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import * as html2pdf from 'html2pdf.js';
import * as jsPDF from 'jspdf';

@Component({
  selector: 'app-aggregate-pdf',
  templateUrl: './aggregate-pdf.component.html',
  styleUrls: ['./aggregate-pdf.component.scss']
})
export class AggregatePdfComponent implements OnInit {
  @ViewChild('content', { static: false }) el!: ElementRef
  participantId: any = [];
  templateId: any;
  aggregateParticpant: any = [];
  aggreagateMembers: any = [];
  result: any;
  aggregateName: any;
  participantRoles: any;
  aggregateReult: any;
  constructor(private dataService: DataService,
    public activeRouter: ActivatedRoute,
    private configuration: Configuration,
  ) {
  }

  addLineBreak(event: KeyboardEvent, i: number, j: number, k?: number, l?: number): void {
   
  
    const elementId = k !== undefined && l !== undefined
      ? `editable-paragraph-member-${i}-${k}-${l}`
      : `editable-paragraph-self-${i}-${j}`;
  
    const paragraph = document.getElementById(elementId);
    
  
    if (paragraph) {
      const selection = window.getSelection();
      const range = selection.getRangeAt(0);
      const br = document.createElement('br');
      range.insertNode(br);
      range.setStartAfter(br);
      range.setEndAfter(br);
      selection.removeAllRanges();
      selection.addRange(range);
      event.preventDefault();
    }
  }
  

  

  ngOnInit() {
    this.activeRouter.queryParams.subscribe((queryParams) => {
      this.participantId = queryParams.partId;
      this.templateId = queryParams.temp;
    });
    this.getAggeregateData();
  }


  getAggeregateData() {
    this.dataService.getById(this.configuration.aggregate, this.participantId).subscribe((res: any) => {
      this.aggregateName = res.name
      this.aggregateParticpant = res.participant.data;
      this.aggreagateMembers = res.members;
      this.groupingData();
    })
  }


  groupingData() {
   this.aggregateReult = this.aggregateParticpant.map(participantItem => {
      const matchingMembers = this.aggreagateMembers
        .map(aggregateMember => aggregateMember.data)
        .flat()
        .filter(memberItem => memberItem.question_id._id === participantItem.question_id._id);
    
      const particpantAns = [participantItem.questionAnswer];
      const memberAns = matchingMembers.reduce((acc, memberItem) => {
        const category = memberItem.member_id.catogory;
        const answer = memberItem.questionAnswer;
    
        const existingCategory = acc.find(item => item.category === category);
        if (existingCategory) {
          existingCategory.answer.push(answer);
        } else {
          acc.push({ category, answer: [answer] });
        }
    
        return acc;
      }, []);
    
      return {
        question_id: participantItem.question_id,
        particpantAns,
        memberAns,
      };
    });
        
    
  }

  
  generatePDF() {
    const doc = new jsPDF()
    const content = this.el.nativeElement;
    const options = {
      margin: 20,
      filename: 'my-document.pdf',
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' }
    };
    const data = html2pdf().set({
      pagebreak: { mode: 'avoid-all' }
    }).from(content).set(options).outputPdf().save()
  }
}

