import { Component, OnInit, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { DatePipe } from '@angular/common';
import { Http } from '@angular/http';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { ExcelService } from './../../shared/services/excel-service';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { MatDialog } from '@angular/material/dialog';
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from 'ngx-toastr';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';
import { element } from 'protractor';

@Component({
  templateUrl: "./user-list.component.html",
  styleUrls: ["./user-list.component.scss"]
})

export class UserListComponent implements OnInit {

  userId: string;
  userList = [];
  showAdminContent: boolean = true;
  showManagerContent: boolean = true;
  totalRecords: any;
  userSearchValue: any;
  isActive: boolean;
  roleName: any;
  excel = [];
  displayedColumns: string[] = ['position', 'userName', 'firstname', 'lastname', 'role', 'email', 'createdDate', 'actions'];
  @ViewChild(MatSort, { static: true }) sorting: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  pageSizeOptions: number[] = [5, 10, 25, 100];
  pageNumber = 1;
  confirmDialogRef: any;

  constructor(
    private router: Router,
    private configuration: Configuration,
    private dataService: DataService,
    private storageConfiguration: StorageConfiguration,
    private toastr: ToastrService,
    private excelService: ExcelService,
    private _matDialog: MatDialog,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit() {
    this.showAdminContent = true;
    this.showManagerContent = false;
    this.getUserList()
    this.roleName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.role)
  }

  viewAsAdmin(isAdmin: boolean) {
    if (isAdmin) {
      this.showAdminContent = true;
      this.showManagerContent = false;
    } else {
      this.showAdminContent = false;
      this.showManagerContent = true;
    }
  }

  getUserList() {
    this.spinner.show();
    this.dataService.getAll(this.configuration.user + '?sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
      let userList = data.data;
      this.totalRecords = data.totalRecords;
      this.userList = this.userListTableData(userList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  nextPage(event) {
    this.spinner.show();
    this.pageNumber = event.pageIndex + 1;
    this.dataService.getAll(this.configuration.user + '?pageNumber=' + this.pageNumber + '&sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
      let userList = data.data;
      this.totalRecords = data.totalRecords;
      this.userList = this.userListTableData(userList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  sortTable() {
    this.spinner.show();
    this.dataService.getAll(this.configuration.user + '?sortBy=' + this.sorting.active + '&orderBy=' + this.sorting.direction).subscribe((data: any) => {
      let userList = data.data;
      this.userList = this.userListTableData(userList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  async searchUser() {
    if (this.userSearchValue != undefined && this.userSearchValue != "") {
      await this.dataService.getAll(this.configuration.user + '?fullName=' + this.userSearchValue + '&sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
        let userList = data.data;
        this.totalRecords = data.data.length;
        this.pageNumber = 1;
        this.paginator.pageIndex = 0;
        this.userList = this.userListTableData(userList);
      }, err => {
        this.spinner.hide();
      });
    } else {
      this.getUserList()
    }
  }

  userListTableData(userData) {
    let userListData = [];
    userData.forEach((element, index) => {
      userListData.push({
        id: element._id,
        position: (index + 1) + (this.pageNumber - 1) * 10,
        role: element.role.displayName,
        firstname: element.firstName,
        lastname: element.lastName,
        userName: element.firstName + ' ' + element.lastName,
        userProfile: element.profileImageURL ? element.profileImageURL : '',
        email: element.email,
        mobile: element.address.mobile,
        createdDate: element.createdDate,
        isActive: element.isActive == true
      });
    });

    return userListData;
  }

  userStatusToggle(event, userId) {
    let userStatus = event.checked == true ? 'Activate' : 'Deactivate';
    let message = {
      message: 'Do you want to ' + userStatus + ' this user?'
    }
    this.confirmDialogRef = this._matDialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      data: message
    });

    this.confirmDialogRef.afterClosed().subscribe(result => {
      let editElement = this.userList.findIndex(element => element.id == userId);
      if (result == true) {
        this.userList[editElement].isActive = event.checked;
        this.userActivateDeactivate(event, userId);
      } else {
        this.userList = JSON.parse(JSON.stringify(this.userList));
      }
    })
  }

  userActivateDeactivate(event, userId) {
    if (event.checked === false) {
      let updateModel = [
        {
          key: "isActive",
          value: false
        },
        {
          key: "isDelete",
          value: true
        }
      ]
      this.dataService.updateSwitch(userId, this.configuration.user + '/', updateModel).subscribe(
        (data: any) => {
          this.toastr.error('Account de-activated successfully.')
        }
      )


    }
    else {
      let updateModel = [
        {
          key: "isActive",
          value: true
        },
        {
          key: "isDelete",
          value: true
        }
      ]
      this.dataService.updateSwitch(userId, this.configuration.user + '/', updateModel).subscribe(
        (data: any) => {
          this.toastr.success('Account activated successfully.')
        }
      )
    }
  }

  export() {
    let datePipe = new DatePipe('en-US');
    this.dataService.getAll(this.configuration.user).subscribe((data: any) => {
      data.data.forEach(row => {
        this.excel.push({
          "User Name": row.fullName,
          "Email": row.email,
          "Role": row.role.displayName,
          "Mobile": row.addressInfo.mobile,
          "Created Date": datePipe.transform(row.createdDate, 'dd/MM/yyyy'),
        });
      });

      this.excelService.exportAsExcelFile(this.excel, 'user_list');
    }, err => {
    })
  }

  adduser() {
    if (this.roleName === 'super-admin' || this.roleName === 'admin') {
      this.router.navigate(['/map/users/user-create'], { queryParams: { isEdit: false, isSuperAdmin: true } });
    }
  }

  edituserdetail(id) {
    if (this.roleName === 'super-admin' || this.roleName === 'admin') {
      this.router.navigate(['/map/users/user-edit'], { queryParams: { userId: id, isEdit: true, isSuperAdmin: true } });
    } else {
      this.router.navigate(['/map/users/user-edit'], { queryParams: { userId: id, isEdit: true, isSuperAdmin: false } });
    }

  }

}


export interface listOfUsers {
  id: any,
  position: any,
  firstname: any,
  lastname: any,
  email: any,
  mobile: any,
  city: any,
  state: any,
  zipcode: any,
  action: any
}

const ELEMENT_DATA: listOfUsers[] = [];


