import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MasterPageComponent } from "./master-page.component";
import { AuthGuardService } from '../auth/service/auth-guard.service';

const routes: Routes = [
  {
    path: 'map',

    component: MasterPageComponent,

    children: [
      {
        path: 'users',
        loadChildren: () => import('../user/user.module').then(m => m.UserModule),
        // canActivate: [AuthGuardService],
      },
      {
        path: 'update-profile',
        loadChildren: () => import('../update-profile/update-profile.module').then(m => m.UpdateProfileModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'change-password',
        loadChildren: () => import('../change-password/change-password.module').then(m => m.ChangePasswordModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'dashboard',
        loadChildren: () => import('../dashboard/dashboard.module').then(m => m.DashboardModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'workshop',
        loadChildren: () => import('../workshop/workshop.module').then(m => m.WorkshopModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'category',
        loadChildren: () => import('../category/category.module').then(m => m.CategoryModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'questionarie',
        loadChildren: () => import('../questionarie-pdf/questionarie.module').then(m => m.QuestionarieModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'organization',
        loadChildren:() => import('../organization/organization.module').then(m => m.OrganizationModule),
        canActivate: [AuthGuardService],
      },
      {
        path: 'survey',
        loadChildren:() => import('../survey/survey.module').then(m => m.SurveyModule),
        canActivate: [AuthGuardService],
      },
      {
       path: 'groups',
       loadChildren:()=> import('../gropus/gropus.module').then(m =>m.GropusModule),
       canActivate: [AuthGuardService]
      }

    ]

  }];

@NgModule({

  imports: [RouterModule.forChild(routes)],

  exports: [RouterModule]

})

export class MasterPageRoutingModule { }