import { Injectable } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class CommonValidation {
    getValidation(invalidData) {
        let validateArray = [];
        let fields = this.validationList();
        invalidData.forEach(element => {
            let fieldValue = fields.filter(x => x.id == element);
            fieldValue.forEach(element => {
                validateArray.push(element.value);
            });
        });
        return validateArray;

    }

    validationList() {
        let requiredFields = [];
        requiredFields = [
            { id: 'roleName', value: 'Role' },
            { id: 'emailId', value: 'Email' },
            { id: 'firstName', value: 'First Name' },
            { id: 'lastName', value: 'Last Name' },
            { id: 'address', value: 'Address' },
            { id: 'mobile', value: 'Mobile' },
            { id: 'city', value: 'City' },
            { id: 'state', value: 'State' },
            { id: 'zipCode', value: 'Zipcode' },
            { id: 'country', value: 'Country' },
        ];

        return requiredFields;
    }

}
