import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatDialogRef } from '@angular/material/dialog';
import { UntypedFormGroup, UntypedFormBuilder, Validators } from '@angular/forms';
import { DataService } from '../services/data-service';
import { Configuration } from 'src/app/app.configuration';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-possible-variants',
  templateUrl: './possible-variants.component.html',
  styleUrls: ['./possible-variants.component.scss']
})
export class PossibleVariantsComponent implements OnInit {

  givenWord: string;
  variantForm: UntypedFormGroup;
  variantList = [];
  constructor(
    private fb: UntypedFormBuilder,
    private dataService: DataService,
    private configuration: Configuration,
    public dialogRef: MatDialogRef<PossibleVariantsComponent>,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,

    @Inject(MAT_DIALOG_DATA) private _data: any,) {
    this.variantForm = this.fb.group({
      variantRootWord: ['', Validators.required],
    })
  }

  ngOnInit() {
    this.variantForm.controls['variantRootWord'].setValue(this._data);
    this.spinner.show();
    this.dataService.getSingleUser(this._data, this.configuration.variations).subscribe(
      (data: any) => {
        this.variantList = data;
        this.spinner.hide();
      }, err => {
        this.toastr.error('Variant search Failed');
        this.spinner.hide();
      });
  }

  addToSynonyms() {
    let sendData = {
      toAdd: true,
      sendData: this.variantList
    }
    this.dialogRef.close(sendData)
  }

  closeDialog() {
    let sendData = {
      toAdd: false,
    }
    this.dialogRef.close(sendData)
  }

  deleteItem(variant) {
    let findIndex = this.variantList.indexOf(variant);
    if (findIndex > -1) {
      this.variantList.splice(findIndex, 1);
    }
  }

  checkVariants() {
    if (this.variantForm.value.variantRootWord.trim() == '') {
      this.toastr.error('Please enter the required fields.');
      return
    }
    if (this.variantForm.valid) {
      this.spinner.show();
      this.dataService.getSingleUser(this.variantForm.value.variantRootWord.trim(), this.configuration.variations).subscribe(
        (data: any) => {
          this.variantList = data;
          this.spinner.hide();
        }, err => {
          this.toastr.error('Variant search Failed');
          this.spinner.hide();
        });
    } else {
      for (const i in this.variantForm.controls) {
        this.variantForm.controls[i].markAsDirty();
        this.variantForm.controls[i].updateValueAndValidity();
      }
      this.toastr.error('Please enter the required fields.');
    }


  }

  restrictToAlphabets(event: any) {
    const pattern = /^[a-zA-Z \-\']+/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
}
